import uniList from "@/components/uni-list/uni-list.vue"
import uniListItem from "@/components/uni-list-item/uni-list-item.vue"
export default {
	data() {
		return {
			loginin: true,
			userInfo: '',
			OpenId: '',
			show1:false,
		}
	},
	components: {
		uniList,
		uniListItem
	},
	methods: {
		//获取用户信息
		wxGetUserInfo() {
			let _this = this;
			uni.getUserInfo({
				provider: 'weixin',
				success: function(infoRes) {
					_this.login()
				},
				fail(res) {
					console.log('用户拒绝授权')
				}
			});
		},

		//获取用户手机号//暂无权限
		wxGetphone: function(e) {
			console.log(JSON.stringify(e.encryptedData));
			console.log(JSON.stringify(e.iv));
		},
		login: function() {
			let _this = this;
			uni.showLoading({
				title: '登录中...'
			});
			uni.login({
				provider: 'weixin',
				success: function(loginRes) {
					let code = loginRes.code;
					uni.request({
						url: _this.$loginUrl + '/api/v1/login', //仅为示例，并非真实接口地址。
						data: {
							code: code,
						},
						header: {
							'custom-header': 'hello' //自定义请求头信息
						},
						success: (res) => {
							_this.OpenId = res.data.openid
							getApp().globalData.token = res.data.session_key
							try {
								uni.setStorageSync('storage_key', res.data.session_key);
							} catch (e) {
								// error
							}
							uni.getSetting({
								success(res) {
									if (res.authSetting['scope.userInfo']) {
										_this.loginin = false;
										// 已经授权，可以直接调用 getUserInfo 获取头像昵称
										//非第一次授权获取用户信息
										wx.getUserInfo({
											success: function(res) {
												_this.userInfo = res.userInfo;
												getApp().globalData.userInfo = res.userInfo;
												_this.updateUserInfo();
											},
										})
									} else {
										console.log('用户未授权')
									}
								}
							})
							_this.show1=true;
							uni.hideLoading();
						}
					});
				}
			});
		},
		Forward: function() {
			uni.navigateTo({
				url: '/pages/forward/forward'
			});
		},

		stocks() {
			uni.navigateTo({
				url: '/pages/stocks/stocks'
			});
		},
		userall() {
			uni.navigateTo({
				url: '/pages/userall/userall'
			});
		},
		// 向后台更新信息
		updateUserInfo() {
			let _this = this;
			uni.request({
				url: _this.$loginUrl + '/api/v1/updateUserInfo', //服务器端地址
				data: {
					openid: _this.OpenId,
					userInfo: _this.userInfo,
					session_key: uni.getStorageSync('storage_key')
				},
				method: 'POST',
				header: {
					'content-type': 'application/json'
				},
				success: (res) => {
					
				}

			});
		},
		onLoad() {
			this.login()
		}
	},


}
